/*
*个人中心组建
*/
<template xmlns:v-bind="http://www.w3.org/1999/xhtml">

  <div>
    <!--头像-->
    <div class="user-center flex_row" @click="needUser=!needUser;showLogin=false">
      <Icon type="md-person" color="#979797" size='38' v-if="!hasLogin"/>
      <Icon type="md-person" color="#0083FF" size='38' v-if="hasLogin"/>
    </div>
    <!--个人中心-->
    <div class="info-box" v-if="needUser">
      <div class="arrow"></div>
      <div class="detail-info-box flex_row" v-if="!hasLogin&&!showLogin">
        <div class="tips" @click="toggleLogin">登录</div>
        <div class="tips loginBtn" @click="toggleRegister">注册</div>
      </div>
      <!--已登入-->
      <div class="detail-info-box flex_row" v-if="hasLogin">
        您好，<div class="tips">{{username}}</div>
        <div class="tips loginBtn" @click="exitLogin">退出登录</div>
      </div>
      <!--用户登录-->
      <div v-if="showLogin" class="detail-info-box flex_row">
        <div class="loginbox flex_col">
          <div class="line flex_row">
            <Input class="input"
                   placeholder="用户名"
                   v-model="username"
                   :disabled="disabledLogin"
                   prefix="ios-contact"
                   clearable
            />
            <Icon type="ios-checkmark-circle-outline" color="#fff" size="16" v-if="l_success==1"/>
            <Icon type="ios-close-circle-outline" color="#fff" size="16" v-if="l_success==2"/>
          </div>
          <div class="line flex_row">
            <Input class="input"
                   placeholder="密码"
                   type="password"
                   v-model="password"
                   :disabled="disabledLogin"
                   prefix="ios-lock"
                   clearable
            />
            <Icon type="ios-checkmark-circle-outline" color="#fff" size="16" v-if="l_success==1"/>
            <Icon type="ios-close-circle-outline" color="#fff" size="16" v-if="l_success==2"/>
          </div>
          <div class="btnBox flex_row">
            <Button  class="btn1 flex_row" @click="loginIn" long shape="circle">登录</Button>
            <div  class="btn2" @click="toggleRegister" >注册</div>
          </div>
        </div>

        <!--加载中-->
        <Spin fix size="large" v-if="showLoLoad"></Spin>
      </div>
    </div>

    <!--用户注册-->
    <Drawer title="用户注册"  v-model="showRegister" class="registerBox" @on-visible-change="reVC">
      <div>
        <div class="flex_row line" v-for="(b, bIndex) in registerBox">
          <div class="label">{{b.label}}：</div>
          <Input class="input"
                 :placeholder="'请输入'+b.label+'…'"
                 :type="b.lType"
                 v-model="registerVal[bIndex]"
                 :disabled="disabledRegister"
                 clearable
                 @on-focus="reInputFocus(bIndex)"
          />
          <Icon type="ios-checkmark-circle-outline" color="green" size="16" v-if="r_right[bIndex]==1"/>
          <Icon type="ios-close-circle-outline" color="red" size="16" v-if="r_right[bIndex]==2"/>
        </div>
      </div>
      <div class="btnBox flex_row">
        <Button  class="btn" @click="showRegister=false">取消</Button>
        <Button  type="primary" class="btn" @click="registerUser">注册</Button>
      </div>
      <!--加载中-->
      <Spin fix size="large" v-if="showReLoad"></Spin>
      <div v-if="showReTips" class="tip">
        请确认输入值合法后，再重试
      </div>
    </Drawer>

  </div>
</template>


<script>
  import { mapGetters,mapActions } from 'vuex'
  export default{
    name: 'userCenter',
    data() {
      return {
        hasLogin:false,//已登录
        needUser:false,


        disabledRegister:false,//注册输入禁用
        showRegister:false,//显示注册抽屉
        registerBox:[
          {label:'用户名称',val:'username',lType:'text'},
          {label:'手机号码',val:'phone',lType:'text'},
          {label:'邮箱地址',val:'email',lType:'text'},
          {label:'真实姓名',val:'truename',lType:'text'},
          {label:'密码',val:'password_',lType:'password'},
          {label:'确认密码',val:'_password',lType:'password'},
          /*{
            "address_": "string",
            // "email": "string",
            "grade": "string",
            // "password_": "string",
            // "phone": "string",
            // "truename": "string",
            "userid": 0,

          }*/
        ],
        blankRe:[null,null,null,null,null,null],//初始状态
        registerVal:[null,null,null,null,null,null],
        r_right:[0, 0, 0, 0, 0, 0],//显示√
        showReTips:false,//显示提示
        showReLoad:false,

        showLogin:false,//显示用户登录输入框
        disabledLogin:false,//禁止输入
        username:null,
        password:null,
        l_success:0,//登录成功
        showLoLoad:false,
      }
    },
    components: {},
    computed: {},
    props: {

    },
    created(){
    },
    mounted(){

    },
    methods: {
      ...mapActions( //mapActions需要写在methods里面
        ['updateUsername','updateNeedRefreshTarget']),// 取得这些方法
      //个人中心开关
      toggleUser(){
        this.needUser = !this.needUser
      },


      //打开用户注册
      toggleRegister(){
        this.showRegister=true;
        this.r_right = [0, 0, 0, 0, 0, 0]
      },
      //用户注册输入框聚焦
      reInputFocus(i){
        var r_right = this.r_right;
        r_right[i]=0;
        this.r_right = r_right
      },
      //用户注册
      registerUser(){
        var registerVal = this.registerVal;
        var registerBox = this.registerBox;
        var reObj = {};
        var $this = this;
        $this.disabledRegister = true;  //禁用输入
        $this.showReLoad = true;
        for(var i=0;i<registerVal.length;i++){
          var key = registerBox[i]['val'];
          var val = registerVal[i];
          var r_right = $this.r_right;
          switch (key) {
            case 'username':
              if(val){
                r_right[i] = 1;
                $this.r_right = r_right;
              }else{
                r_right[i] = 2;
                $this.r_right = r_right;
              }

              break;
            case 'phone':
              if(!(/^1[3456789]\d{9}$/.test(val))){
                r_right[i] = 2;
                $this.r_right = r_right
              }else{
                r_right[i] = 1;
                $this.r_right = r_right
              }
              break;
            case 'email':
              if(!(/^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/.test(val))){
                r_right[i] = 2;
                $this.r_right = r_right
              }else{
                r_right[i] = 1;
                $this.r_right = r_right
              }
              break;
            case 'truename':
              if(!(/^[\u4e00-\u9fa5]+$/.test(val))){
                r_right[i] = 2;
                $this.r_right = r_right
              }else{
                r_right[i] = 1;
                $this.r_right = r_right
              }

              break;
            case 'password_':
              if(val){
                r_right[i] = 1;
                $this.r_right = r_right;
              }else{
                r_right[i] = 2;
                $this.r_right = r_right;
              }
              break;
            case '_password':
              var p = reObj['password_'];
              if (p && p == val) {
                r_right[i] = 1;
                $this.r_right = r_right;

              } else {
                r_right[i] = 2;
                $this.r_right = r_right;
              }
              break;
          }
          reObj[key] = val
        }
        if ($this.r_right.join('') == '111111'){
          $this.showReTips = false;//显示提示
          // $this.showReLoad = false;
          //调用用户注册接口
          delete reObj._password;
          $this.$api.registerUser(reObj).then(function (res) {
            if(res){
              $this.$Message.success(reObj['username']+'，注册成功，请登录');
              $this.showRegister = false
            }
          }).catch(function (err) {
            $this.$Message.warning('注册失败请重试');
            $this.showReLoad = false;
            $this.disabledRegister = false;  //禁用输入
          })
        }else{
          //输入错误
          $this.showReTips = true;//显示提示
          $this.showReLoad = false;
          $this.disabledRegister = false;  //禁用输入
        }

      },
      //用户注册drawn显示状态改变
      reVC(e){
        if (!e){//关闭
          this.registerVal = this.blankRe//恢复初始状态
        }else{
          this.disabledRegister = false;  //禁用输入
          this.showReTips = false;//显示提示
          this.showReLoad = false
        }
      },

      //打开用户登录输入框
      toggleLogin(){
        if(!this.hasLogin){//未登录时
          this.showLogin = true;
          this.disabledLogin = false;
          this.showLoLoad = false;
          this.username = null;
          this.password = null;
          this.l_success = 0;
        }else{
          //已登录
        }

      },
      //用户登录
      loginIn(){
        var $this = this;
        var username = $this.username;
        var password = $this.password;
        if (username&&password){
          $this.showLoLoad = true;
          $this.disabledLogin = true;

          var loObj = {
            username:username,
            password_:password
          };
          $this.$api.loginIn(loObj).then(function (res) {
            $this.showLoLoad = false;
            if(res=='登录成功'){
              $this.$Message.success('登录成功！');
              $this.l_success = 1;
              $this.hasLogin = true;
              $this.showLogin = false;
              $this.updateUsername(username)
              $this.updateNeedRefreshTarget(new Date().getTime().toString())
            }else{
              $this.disabledLogin = false;
              $this.l_success = 2;
              $this.$Message.warning('用户名或密码错误');
            }
          }).catch(function (err) {
            $this.showLoLoad = false;
            $this.disabledLogin = false;
            if(err.response.data=='登录失败'){
              $this.l_success = 2;
              $this.$Message.warning('用户名或密码错误');
            }else{
              $this.l_success = 0;
              $this.$Message.warning('登录失败，请重试');
            }
          })
        }else{
          $this.$Message.warning('请输入完整！');
        }
      },
      //退出登录
      exitLogin(){
        this.hasLogin = false;
        this.toggleLogin();
        this.updateUsername('user_admin');
        this.updateNeedRefreshTarget(new Date().getTime().toString())
      }
    },
    beforedestroy(){

    },
    watch: {

    }
  }
</script>

<style scoped>

  /*用户中心*/
  .user-center {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    box-shadow: 1px 2px 1px rgba(0,0,0,.15);
    cursor: pointer;
    position: absolute;
    right: 0;
    background-color: #fff;
  }


  /*个人中心*/
  .info-box {
    width: 260px;
    position: absolute;
    right: 0;
    top: 65px;
  }
  .info-box .arrow {
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 7px 8px;
    border-color: transparent transparent #3385ff;
    position: absolute;
    top: -8px;
    right: 16px;
  }
  .info-box .detail-info-box {
    border-radius: 3px;
    background: #3385ff;
    width: 100%;
    height: auto;
    padding: 10px 0;
    min-height: 65px;
    box-shadow: 1px 2px 1px rgba(0,0,0,.15);
    color: #fff;
  }
  .info-box .detail-info-box .tips{
    font-size: 14px;
    border-bottom: 1px solid #fff;
    cursor: pointer;
  }
  .info-box .detail-info-box .loginBtn{
    margin-left: 1rem;
  }


  /*用户登录*/
  .loginbox{
    width: 70%;
    height: 100%;
  }
  .loginbox .line{
    margin-top: 5px;
    width: 100%;
  }
  .loginbox .btnBox{
    margin-top: 5px;
    width: 100%;
  }
  .loginbox .btnBox .btn1{
    /*color: #3385ff;*/
  }
  .loginbox .btnBox .btn2{
    margin-left: 5px;
    font-size: 14px;
    border-bottom: 1px solid #fff;
    cursor: pointer;
    color: #fff;
    width: 40px;
  }
  /*用户注册*/
  .registerBox{

  }
  .registerBox .line{
    margin-top: 1rem;
  }

  .registerBox .line .label{
    font-weight: bold;
    flex-shrink: 1;
    width: 6.5rem;
    font-size: 0.8rem;
  }
  .registerBox .btnBox{
    margin-top: 2rem;
  }
  .registerBox .btnBox .btn{
    margin-left: 0.8rem;
  }
  .registerBox .tip{
    text-align: center;
    margin-top: 0.5rem;
    color: red;
  }
</style>



