/*
*siteSearch 标准地名搜索页
*/

<template>
  <div class="editWrapper">
    <!--编辑框-->
    <edit-box  class="leftView"></edit-box>
    <!--导入导出-->
    <div class="rightBox flex_row">
      <Upload :action="upLoadUrl" class="flex_row" ref="upload" v-if="username"
              name="fileName"
              accept="file"
              :before-upload="beforeUpload"
              :on-success="onUpSuccess"
              :on-error="onUpError"
              :show-upload-list="false"
              multiple
      >
        <Button  class="item" icon="md-arrow-round-up" title="多个文件推荐使用zip">导入</Button>
      </Upload>
      <Button  class="item" icon="md-arrow-round-up" title="多个文件推荐使用zip" @click="importFile" v-if="!username">
        导入
      </Button>
      <div class="item flex_col">
        <Button icon="md-download" @click="downloadFile">导出</Button>
      </div>


    </div>

    <Spin fix style="background: rgba(255,255,255,.5);" v-if="showLoading">
      <loading msg="上传中……"></loading>
    </Spin>

    <!--个人中心-->
    <user-center class="userView"></user-center>
  </div>
</template>

<script>
  import editBox from '../components/editBox.vue'
  import loading from '../components/loading'
  import { mapGetters,mapActions } from 'vuex'
  import userCenter from '../components/userCenter'
  export default {
    name: 'home',
    components: {
      editBox,loading,userCenter
    },
    created(){
    },
    data () {
      return {
        showLoading:false,
        openTarget:false,//打开设置列表
        targetData:null,//目标数据库
        targetListIndex:0, //1为设置，2为导出
      }
    },
    computed:{
      ...mapGetters(['username','tablename']),
      upLoadUrl(){
        return EDITSERVICE['uploadUrl']+'?username='+this.username
      }
    },
    methods:{
      ...mapActions(['updateNeedRefreshTarget']),
      importFile(){
        if(!this.username){
          this.$Message.warning('请先登录');
          return false
        }
      },
      //导入前
      beforeUpload(file){
      },
      //导入成功
      onUpSuccess(res, file, fileList) {
        this.$Message.success('数据上传成功!');
        this.showLoading=false;
        this.updateNeedRefreshTarget(new Date().getTime().toString())
      },
      //导入失败
      onUpError(error, file, fileList){
        this.showLoading=false;
        this.$Message.error( '导入失败，请重试！');
      },
      //下载
      downloadFile() {
        if(this.username){
          if(this.tablename){
            var $this = this;
            $this.$Message.loading({
              content:'正在创建下载链接，请稍后……',
              duration:0
            });
            $this.$api.exportFile($this.tablename, $this.username).then(function (res) {
              if (res) {
                $this.$Message.destroy();
                download(res,$this.tablename)
              }else{
                $this.$Message.warning('下载链接创建失败，请重试！');
              }
            }).catch(function (err) {
              if (err.response.data) {
                $this.$Message.warning(err.response.data)
              }
            })
          }else{
            this.$Message.warning('请先选择数据源！');
          }
        }else{
          this.$Message.warning('请先登录！');
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .editWrapper{
    width: 100%;
    height: 100%;
    overflow-y: hidden;
  }
  .leftView {
    position: absolute;
    left: 20px;
    z-index: 100;
  }
  .userView {
    z-index: 10000;
    position: absolute;
    top: 20px;
    right: 20px;
  }
  .rightBox{
    position: absolute;
    z-index: 100;
    right: 80px;
    top: 29px;
  }
  .rightBox .item{
    margin-right: 5px;
    box-shadow: 1px 2px 1px rgba(0,0,0,.15);
    border-radius: 2px;
    background: #fff;
  }

  /*设置列表*/
  .tools{
    background: #fff;
    padding: 2px 10px;
    box-shadow: 1px 2px 1px rgba(0,0,0,.15);
    border-radius: 2px;
    position: absolute;
    top: 75px;
    right: 85px;
    width: 155px;
    z-index: 100;
  }
  .arrow {
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 7px 8px;
    border-color: transparent transparent #fff;
    position: absolute;
    top: -8px;
    right: 10px;
  }
</style>



