/*
*编辑框
*/

<template xmlns:v-bind="http://www.w3.org/1999/xhtml">
  <div>
    <!--非标准地址列表-->
    <div class="listView">
      <!--地址匹配-->
      <!--<div class="selector flex_row" v-if="!username">
        <div class="block flex_row">
          &lt;!&ndash;选择街道&ndash;&gt;
          <span class="label">街道：</span>
          <select v-model="curStreet" class="select" @change="onSelStreet">
            <option v-for="s in allStreets">
              {{s}}
            </option>
          </select>
        </div>

        <div class="block flex_row">
          <span class="label">社区：</span>
          &lt;!&ndash;选择社区&ndash;&gt;
          <select v-model="curCommunity" class="select" @change="onSelStreet">
            <option v-for="c in allCommunity">
              {{c.community}}
            </option>
          </select>
        </div>
        <div class="flex_row toggle">
          <span v-show="showList" @click="showList=!showList">▲</span>
          <span v-show="!showList" @click="showList=!showList">▼</span>
        </div>
      </div>-->


      <!--地址编辑-->
      <div class="selector flex_row">
        <div class="block flex_row editBlock">
          <!--选择街道-->
          <span class="label">数据源：</span>
          <Select v-model="curTarget" class="select" @on-change="onSelTarget" :title="curTarget">
            <Option  v-for="t,ti in targetList" :key='ti' :title="t" class="ellipsis" :value="t">
              {{t}}
            </option>
          </Select>
        </div>
      </div>
      <!--loading spin-->
      <small-spin v-if="needSpin" msg="正在加载……"></small-spin>

      <!--列表-->
      <ul v-show="showList" class="siteList">
        <li class="flex_row" @click="toggleEditModal(l.fid,index)" v-for="(l,index) in listArr"
            :class="{sel:curEditIndex==index}"
        >
          <span>{{l.origin_address}}</span>
          <img src="../../static/images/map/edit.png" class="imgItem">
        </li>
        <Page :current='page' :total="listCount" show-total size="small" class-name="pageClass" @on-change="pageChanged"/>
      </ul>

    </div>

    <!--地址匹配对话框-->
    <Modal
      v-model="showEditModal"
      title="地址编辑"
      :mask="false"
      :styles="{
          width: '380px',
          position: 'absolute',
          top: '75px',
          right: '20px',
          maxHeight:'80%'}"
      scrollable :visible="editVisible"
    >

      <div v-for="(val,index) in editObj" class="editLine flex_row"
           :class="{autoHeightWrap:val.type=='textarea'}">
        <div class="label flex_row">
          <span v-html="val.label"></span>
        </div>


        <input type="text" v-model="editValue[val.val]" :title="editValue[val.val]"
               :class="val.disabled?'text':'text canEdit'"
               :disabled="val.disabled" v-if="val.type=='text'"/>
        <div class="autoHeight"
             v-if="val.type=='textarea'">
          {{editValue[val.val]}}
        </div>

      </div>


      <!--相似标准地址-->
      <div class="editLine flex_row  autoHeightWrap">
        <div class="label flex_row">
          <span>可选标准地址</span>
        </div>
        <div class="selectWrapper autoHeight flex_row">
          <Select v-model="curSelAddress" class="select"
                  placeholder="无匹配数据"
          >
            <Option v-for="(s,index) in matchResult" :value="s.address+':'+s.similarity" :key="index"
                    :label="s.address">
              <span>{{ s.address }}</span>
              <Progress v-if="s.similarity"
                :stroke-width="7" title="相似度"
                :percent="s.similarity*100" class="similar_row"/><!--相似度 similarity-->
            </Option>

          </Select>
          <div class="editBtnWrap">
            <Button shape="circle" icon="md-create" title="新增" size="small" @click="toggleAddModal"></Button>
          </div>
        </div>

      </div>

      <!--选择结果-->
      <div class="editLine flex_row autoHeightWrap">
        <div class="label flex_row">
          <span>已选标准地址</span>
        </div>

        <div class="autoHeight flex_row" v-if="curSelAddress">
          <span>{{curSelAddObj.address}}</span>
          <Progress v-if="curSelAddObj.similarity"
            vertical :stroke-width="7" title="相似度"
                    :percent="curSelAddObj.similarity*100" class="similar"/><!--相似度 similarity-->
        </div>
        <div v-if="!curSelAddress" class="autoHeight flex_row" >无匹配数据</div>
      </div>

      <!--按钮-->
      <div slot="footer" class="">
        <Button size="small" @click="backRecord"
                :disabled="page==1&&curEditIndex==0"
        >
          <Icon type="ios-arrow-back"></Icon>
        </Button>
        <Button @click="showEditModal=false">取消</Button>
        <Button type="primary" @click="submitEdit">保存</Button>
        <Button size="small" @click="nextRecord"
                :disabled="(curEditIndex==listCount%10-1||curEditIndex==9)&&(page==Math.ceil(listCount/10))"

        >
          <Icon type="ios-arrow-forward"></Icon>
        </Button>
      </div>
      <Spin fix size="large" v-if="showELoad"></Spin>

    </Modal>

    <!--增加地址窗-->
    <Modal
      v-model="showAddModal"
      title="新增地址  +"
      :styles="{
          width: '380px',
          position: 'absolute',
          top: '75px',
          right: '20px',
          maxHeight:'80%'}"
      scrollable
    >

      <!--历史地址-->
      <div class="editLine flex_row"
      >
        <div class="label flex_row">
          历史地址
        </div>
        <div class="input">
          {{editValue.originAddress}}
        </div>

      </div>

      <div v-for="(addObj,addName,index) in addEditObj" class="editLine flex_row"
           >
        <div class="label flex_row">
          <span v-html="addObj.label"></span>
        </div>
        <Input v-model="addEditValue[addName]"
               :title="addEditValue[addName]"
               :class="addObj.canInput?'input':'cannot input'"
               :readonly="!addObj.canInput"
               :icon="showOption!=addName?'ios-arrow-down':'ios-arrow-up'"
               @on-click="showOption!=addName?showOption=addName:showOption=-1"
               @on-change="addObj.changeHandler(addEditValue[addName])"
        />

        <div class="options" v-if="showOption==addName">
          <RadioGroup v-model="addEditValue[addName]" vertical @on-change="radioChange(addEditValue[addName])">
            <Radio :label="o" v-for="(o,i) in radioArr[addName]" :key="i">
              {{o}}
            </Radio>
          </RadioGroup>


        </div>
      </div>
      <!--新增条目按钮-->
      <div class="editLine flex_row addLine" title="新增其他" @click="showAddBtn=false" v-if="showAddBtn">
        <Icon type="md-add-circle" size="24" color="#25A4F4"/>
      </div>
      <!--新增的其他条目-->
      <div class="editLine flex_row" v-if="!showAddBtn">
        <div class="label flex_row">
          <span>其他</span>
        </div>
        <Input v-model="addEditValue.suffix"
               :title="addEditValue.suffix"
               class="input"
               :icon="showOption!='suffix'?'ios-arrow-down':'ios-arrow-up'"
               @on-click="showOption!='suffix'?showOption='suffix':showOption=-1"
        />
        <Icon type="ios-remove-circle-outline" size="24" color="#25A4F4" title="删除其他" @click="showAddBtn=true"/>

        <div class="options otherOpt" v-if="showOption=='suffix'">
          <RadioGroup v-model="addEditValue['suffix']" vertical @on-change="radioChange">
            <Radio :label="o" v-for="(o,i) in suffixArr" :key="i">
              {{o}}
            </Radio>
          </RadioGroup>
        </div>
      </div>


      <!--按钮-->
      <div slot="footer" class="">
        <Button @click="showAddModal=false">取消</Button>
        <Button type="primary" @click="handlerAddSite">确定</Button>
      </div>

      <Spin fix size="large" v-if="showAddLoad"></Spin>

    </Modal>
  </div>

</template>


<script>
  import {RegularStr} from '../js/render.js'
  import SmallSpin from "./smallSpin";
  import { mapGetters,mapActions } from 'vuex'
  export default {
    name: 'editBox',
    data() {
      return {
        needUser: false,

        needSpin: false,//显示spin
        page: 1,//当前页
        curStreet: '',//当前选择的街道
        allStreets: EDITSELECTORCFG['street'],
        curCommunity: '',//当前选择的社区
        allCommunity: [],


        showList: false,//打开非标准地址列表
        //非标准地址列表
        listArr: [],
        listCount: 0,//非标准地址总数，用于分页
        curEditIndex:-1,//当前选择结果索引


        showEditModal: false,//是否打开编辑浮云
        showELoad:false,//编辑框中的loading
        //编辑对象
        editVisible: false,
        editObj: [
          {'label': '名称', val: 'name_', 'disabled': true, 'necessary': false, type: 'text'},
          {'label': '代码', val: 'code_', 'disabled': true, 'necessary': false, type: 'text'},
          {'label': '街道', val: 'street_', 'disabled': true, 'necessary': false, type: 'text'},
          {'label': '社区', val: 'community_', 'disabled': true, 'necessary': false, type: 'text'},
          {'label': '历史地址', val: 'originAddress', 'disabled': true, 'necessary': false, type: 'textarea'},//JYCS字段

        ],
        editValue: {
          fid:null,
          code_: '',
          name_: '',//企业名称
          street_: '',//街道
          community_: '',//社区
          originAddress: '',//原地址
          lng:'',
          lat:''
        },

        matchResult: [],//相似标准地址
        curSelAddress:'',//当前选择匹配结果，地址:相似度
        curSelAddObj:{},//将curSelAddress转换为obj


        //-------------地址编辑
        showAddLoad:false,
        curTarget:'',
        targetList:[],//数据源列表
        showAddModal:false,//打开编辑框
        addEditObj:{
          street:{'label': '街道', 'necessary': false,canInput:false,
            changeHandler:function () {

            }},
          community:{'label': '社区',  'necessary': false, canInput:false,
            changeHandler:function () {

            }},
          road:{'label': '道路',  'necessary': false, canInput:true,
            changeHandler:this.getRoadLike},
          road_num:{'label': '道路编码',  'necessary': false, canInput:true,
            changeHandler:this.getRoadCodeLike},
          village:{'label': '小区','necessary': false, canInput:true,
            changeHandler:this.getVillageLike},
          // building:{'label': '楼栋',  'necessary': false, options:['选项1','2'],canInput:true},
          code:{'label': '楼栋编码', 'necessary': false, canInput:true,
            changeHandler:this.getCodeLike},
        },
        radioArr:{
          street:EDITSELECTORCFG['street'],
          community:null,
          road:null,//道路
          road_num:null,  //道路编码
          village:null,
          code:null,
        },
        // streetArr:[],//所有街道数组
        // communityArr:[],//所有社区数组
        // roadArr:[],//所有社区数组
        // roadCodeArr:[],//所有社区数组
        // villageArr:[],//小区数组数据量大
        // codeArr:[],

        addEditValue:{
          code:null,
          street:null,
          community:null,
          road:null,//道路
          road_num:null,  //道路编码
          village:null,
          suffix:'',//其他
        },
        showOption:-1,

        //新增其他
        showAddBtn:true,
        suffixArr:[]
      }
    },
    components: {SmallSpin},
    computed: {
      ...mapGetters(['username','needRefreshTarget'])
    },
    props: {},
    created() {
    },
    mounted() {

    },
    methods: {
      ...mapActions(['updateTablename','updateMarkLngLat']),
      //监听到页码变化
      pageChanged(page,option) {
        option=option||{needSelFirst: true};
        var $this = this;
        $this.page = page;
        //地址编辑状态
        $this.getAddByTarget(page,option);
        //调用接口获取当前街道当前页，社区下的所有非标准地址
        // $this.getAddressByCommunity(false,option)
      },

      //地址编辑，选择数据源时
      onSelTarget(){
        var curTarget = this.curTarget;
        var $this = this;
        if (curTarget) {
          $this.updateTablename(curTarget);

          //切换到地址编辑，更新列表信息
          $this.curEditIndex=-1;
          $this.showList=false;
          $this.needSpin = true;//显示正在加载


          //设置工作数据表
          $this.$api.setTarget(curTarget,$this.username).then(function (res) {
            $this.$Message.success('数据源切换成功！');
            //查询总数
            // var tablename = curTarget.split('.')[0];//截取
            $this.$api.getCount_edit($this.username).then(function (res) {
              $this.listCount = res.total;//总数
              //将页面恢复到第1页
              $this.page = 1;
              $this.getAddByTarget($this.page)


            }).catch(function () {
              $this.needSpin = false;//显示正在加载
              $this.listArr=[];
              $this.showList = false;
            });
          }).catch(function (err) {
            if(err.response.data){
              $this.$Message.warning(err.response.data+',切换失败!')
            }
            $this.listArr=[];
            $this.showList = false;
            $this.needSpin = false;//显示正在加载
          })



        }
      },
      getAddByTarget(page,option){
        option=option||{needSelFirst: true};
        var $this = this;
        //查询当前页列表
        $this.$api.getAddressByTarget(page,$this.username).then(function (res) {
          $this.needSpin = false;//显示正在加载
          $this.listArr = res.rows;
          $this.showList = true;
          if (option.needSelFirst) {
            //需要默认选择第一项
            $this.toggleEditModal(res.rows[0]['fid'], 0)
          } else if (option.needSelLast) {
            //需要默认选择最后一项
            $this.toggleEditModal(res.rows[9]['fid'], 9)
          }
        }).catch(function (err) {
          $this.needSpin = false;//显示正在加载
          $this.listArr=[];
          $this.showList = true;
        });
      },
      //选择街道时
      onSelStreet() {
        var curStreet = this.curStreet;
        var $this = this;
        if (curStreet) {
          //调用接口获取当前街道含有的所有社区
          var url = URLCFG['getCommunityByStreetUrl'];
          $this.$api.getMsg(url, {tablename: curStreet}).then(function (res) {
            $this.allCommunity = res.rows;
            $this.curCommunity = res['rows'][0]['community'];
            //调用接口获取当前街道，社区下的所有非标准地址
            $this.getAddressByCommunity(true,{needSelFirst:true})
          })

        }

      },
      //当选择社区时
      onSelCommunity() {
        var curCommunity = this.curCommunity;
        var $this = this;
        if (curCommunity) {
          $this.getAddressByCommunity(true,{needSelFirst:true})
        }

      },
      //调用接口获取当前街道，社区下的所有非标准地址
      getAddressByCommunity(needTotal,option) {
        var $this = this;
        option=option||{};
        $this.curEditIndex=-1;
        $this.showList=false;
        $this.needSpin = true;//显示正在加载

        needTotal = needTotal == undefined ? true : needTotal;//翻页的时候可以不必查询总数
        if (needTotal) {
          //查询总数
          var url = URLCFG['getCountByCommunityUrl'];
          $this.$api.getMsg(url, {tablename: $this.curCommunity}).then(function (res) {
            $this.listCount = res.total;//总数
          });
          //将页面恢复到第1页
          $this.page=1;
        }
        //查询当前页列表
        $this.$api.getAddressByCommunity($this.page, $this.curCommunity).then(function (res) {
          $this.needSpin = false;//显示正在加载
          $this.listArr = res.rows;
          $this.showList = true;
          if (option.needSelFirst) {
            //需要默认选择第一项
            $this.toggleEditModal(res.rows[0]['fid'], 0)
          } else if (option.needSelLast) {
            //需要默认选择最后一项
            $this.toggleEditModal(res.rows[9]['fid'], 9)
          }
        });
      },
      //编辑开关
      toggleEditModal(fid, index) {
        var $this = this;
        $this.showELoad = true;
        $this.curSelAddress = '';

        //调用接口查询编辑信息
        $this.$api.getInfoByFid_edit(fid, $this.username).then(function (res) {
          if (res.rows.length > 0) {
            var d = res.rows[0];
            //地图定位
            if (d.longitude_ && d.latitude_) {
              $this.updateMarkLngLat([d.longitude_, d.latitude_]);
            } else {
              $this.updateMarkLngLat(undefined);
              $this.$Message.warning('没有查询到坐标！');
            }

            $this.editValue = {
              fid: fid,
              code_: d['code_'] ? d['code_'] : '',
              name_: d['name_'] ? d['name_'] : '',
              street_: d['street_'] ? d['street_'] : '',
              community_: d['community_'] ? d['community_'] : '',
              originAddress: d['origin_address'],
              lng:d['longitude_']?d['longitude_']:'',
              lat:d['latitude_']?d['latitude_']:''
            };
            $this.showEditModal = true;
            $this.curEditIndex = index;//记录当前索引，方便下一条

            //调用接口获取匹配地址
            var keyword = d['name_'];
            if(keyword){
              $this.$api.getMatchList(keyword).then(function (res) {
                if (res.rows && res.rows.length > 0) {
                  $this.matchResult = res.rows;
                  $this.curSelAddress = res.rows[0].address + ':' + res.rows[0].similarity;//默认选择第一个
                } else {
                  //没有匹配结果
                  $this.matchResult = [];
                  $this.curSelAddress = ''
                }
                $this.showELoad = false;
              }).catch(function (err) {
                $this.showELoad = false;
                $this.matchResult = [];
                $this.curSelAddress = ''
              });
            }

          } else {
            $this.showELoad = false;
            $this.$Message.warning('获取地址信息失败，请稍后重试！')
          }


        }).catch(function (err) {
          $this.showELoad = false;
          $this.$Message.warning('获取地址信息失败，请稍后重试！')
        });


      },

      //上一条
      backRecord() {
        if (this.curEditIndex > 0) {
          var index = this.curEditIndex - 1;
          var fid = this.listArr[index]['fid'];
          this.toggleEditModal(fid, index);
        } else if (this.page > 1) {
          //执行向前翻页
          this.pageChanged(this.page - 1, {needSelLast:true});
          this.curEditIndex = 9
        }
      },
      //下一条
      nextRecord(){
        var pageCount = Math.ceil(this.listCount/10);//总页数
        if(this.page<pageCount){
          //非最后一页
          if(this.curEditIndex<9) {
            var index = this.curEditIndex + 1;
            var fid = this.listArr[index]['fid'];
            this.toggleEditModal(fid, index);
          }else{
            //往后翻一页
            this.pageChanged(this.page+1,{needSelFirst:true});
            this.curEditIndex = 0
          }
        }else if((this.listCount%10==0&&this.curEditIndex<9)||(this.curEditIndex<this.listCount%10-1)){
          //是最后一页
          //非最后一条时可以点击下一条
          var index = this.curEditIndex + 1;
          var fid = this.listArr[index]['fid'];
          this.toggleEditModal(fid, index);
        }

      },



      //打开地址新增窗口
      toggleAddModal(){
        if(this.username){
          //根据坐标获取标准地址
          function getStandardByLL(){
            $this.$api.getStandardByLL($this.editValue.lng,$this.editValue.lat).then(function (res) {
              if(res.rows.length>0){
                let row = res.rows[0];
                $this.addEditValue = {
                  // code:row.building_id,//楼栋编码
                  code:row.code,//楼栋编码
                  street:row.street,
                  community:row.community,
                  road:row.road,//道路
                  road_num:row.road_num,  //道路编码
                  village:row.village,
                  suffix:'',//其他
                };
                $this.getAllComByStr(row.street,false);
                $this.showAddLoad=false;
              }else{
                setByOrigin()
              }
              $this.showAddLoad=false;
            }).catch(function (err) {
              setByOrigin()
            });
          }
          //直接根据editValue来填充
          function setByOrigin() {
            $this.addEditValue = {
              // code:row.building_id,//楼栋编码
              code:'',//楼栋编码
              street:$this.editValue.street_,
              community:$this.editValue.community_,
              road:'',//道路
              road_num:'',  //道路编码
              village:'',
              suffix:'',//其他
            };
            if($this.editValue.street_) {
              $this.getAllComByStr($this.editValue.street_, false);
            }
            $this.showAddLoad=false;
          }

          this.showAddModal=true;
          this.showAddLoad=true;

          var $this = this;
          $this.$api.getStandardByFid($this.editValue.fid,$this.username).then(function (res) {
            if(res.rows.length>0){
              let row = res.rows[0];
              $this.addEditValue = {
                // code:row.building_id,//楼栋编码
                code:row.code,//楼栋编码
                street:row.street,
                community:row.community,
                road:row.road,//道路
                road_num:row.road_num,  //道路编码
                village:row.village,
                suffix:'',//其他
              };
              $this.getAllComByStr(row.street,false);
              $this.showAddLoad=false;
            }else{
              getStandardByLL()
            }

          }).catch(function (err) {
            getStandardByLL()
          })
        }else{
          this.$Message.warning('请先登录！')
        }


      },
      //根据街道获取社区
      getAllComByStr(street, needSel) {
        var $this = this;
        var addEditValue = JSON.parse(JSON.stringify($this.addEditValue));
        $this.$api.getComByStr(street).then(function (res) {
          var opsArr = [];
          var communityArr = res.rows;
          for (var i = 0; i < communityArr.length; i++) {
            opsArr.push(communityArr[i].community)
          }
          $this.radioArr.community = opsArr;
          if(needSel){
            addEditValue.community = opsArr[0];
            $this.addEditValue = addEditValue;
          }
          //获取小区
          // $this.getVillageByCom(opsArr[0]);
          //获取道路
          $this.getRoadByCommunity(opsArr[0], needSel);
        }).catch(function (err) {
          $this.radioArr.community = [];
          $this.radioArr.road = [];
          $this.radioArr.road_num = [];
          $this.radioArr.village = [];
          $this.radioArr.code = [];
          addEditValue.community = null;
          addEditValue.road = null;
          addEditValue.road_num = null;
          addEditValue.village = null;
          addEditValue.code = null;
          $this.addEditValue = addEditValue;
          $this.$Message.warning('获取' + street + '社区列表失败，请稍后重试')
        })
      },
      //根据社区获取道路
      getRoadByCommunity(community, needSel){
        var $this = this;
        var addEditValue = JSON.parse(JSON.stringify($this.addEditValue));
        $this.$api.getRoadByCommunity(community).then(function (res) {
          var opsArr = [];
          var roadArr = res.rows;
          for (var i = 0; i < roadArr.length; i++) {
            opsArr.push(roadArr[i].road)
          }
          $this.radioArr.road = opsArr;
          if(needSel) {
            addEditValue.road = opsArr[0];  //默认选择第一项
            $this.addEditValue = addEditValue;
          }
          //根据道路获取道路编号
          $this.getRoadCodeByRoad(opsArr[0], needSel);
        }).catch(function (err) {
          $this.radioArr.road = [];
          $this.radioArr.road_num = [];
          addEditValue.road =null;
          addEditValue.road_num =null;
          $this.addEditValue = addEditValue;
          $this.$Message.warning('获取'+community+'道路列表失败，请稍后重试')
        })
      },
      //根据道路获取道路编号
      getRoadCodeByRoad(road,needSel){
        var $this = this;
        var addEditValue = JSON.parse(JSON.stringify($this.addEditValue));
        var community = addEditValue.community;
        $this.$api.getRoadCodeByRoad(community, road).then(function (res) {
          var opsArr = [];
          var roadCodeArr = res.rows;
          for (var i = 0; i < roadCodeArr.length; i++) {
            opsArr.push(roadCodeArr[i].road_num)
          }
          $this.radioArr.road_num = opsArr;
          if(needSel) {
            addEditValue.road_num = opsArr[0];  //默认选择第一个
            $this.addEditValue = addEditValue;
          }
        }).catch(function (err) {
          $this.radioArr.road_num = [];
          addEditValue.road_num =null;
          $this.addEditValue = addEditValue;
          $this.$Message.warning('获取'+road+'道路编号列表失败，请稍后重试')
        })
      },
      //根据社区获取小区
      getVillageByCom(community){
        var $this = this;
        var addEditValue = JSON.parse(JSON.stringify($this.addEditValue));
        $this.$api.getVillByCom(community).then(function (res) {
          var opsArr = [];
          var villageArr = res.rows;
          for (var i = 0; i < villageArr.length; i++) {
            opsArr.push(villageArr[i].village)
          }
          $this.radioArr.village = opsArr;
          addEditValue.village = opsArr[0];  //不需要默认选择第一项
          // addEditValue.code = null;  //不需要默认选择第一项
          $this.addEditValue = addEditValue;
          //获取楼栋编号
          // $this.getCodeByVillage(opsArr[0]);
        }).catch(function (err) {
          $this.radioArr.village = [];
          $this.radioArr.code = [];
          addEditValue.village =null;
          addEditValue.code =null;
          $this.addEditValue = addEditValue;
          $this.$Message.warning('获取'+community+'小区列表失败，请稍后重试')
        })
      },
      //查楼栋编号
      getCodeByVillage(village){
        var $this = this;
        var addEditValue = JSON.parse(JSON.stringify($this.addEditValue));
        var community = addEditValue.community;
        $this.$api.getCodeByVillage(community, village).then(function (res) {
          var opsArr = [];
          var codeArr = res.rows;
          for (var i = 0; i < codeArr.length; i++) {
            opsArr.push(codeArr[i].code)
          }
          $this.radioArr.code = opsArr;
          // addEditValue.code = null;  //不需要默认选择第一项
          // $this.addEditValue = addEditValue;
        }).catch(function (err) {
          $this.radioArr.code = [];
          addEditValue.code =null;
          $this.addEditValue = addEditValue;
          $this.$Message.warning('获取' + community + ','+village+'楼栋编号列表失败，请稍后重试')
        })
      },
      //添加地址窗的radio选择项目变换
      radioChange(val){
        var $this = this;
        var changeName = $this.showOption;
        $this.showOption = -1;
        // var addEditValue = JSON.parse(JSON.stringify($this.addEditValue));
        switch (changeName) {
          case 'street':
            //获取社区
            $this.getAllComByStr(val);
            break;
          case 'community':
            //获取道路
            $this.getRoadByCommunity(val);
            //获取小区
            // $this.getVillageByCom(val);
            break;
          case 'road':
            //获取楼栋编号
            $this.getRoadCodeByRoad(val);
            break;
          case 'road_num':
            break;
          case 'village':
            // $this.getCodeByVillage(val);
            break;
          case 'code':
            break;
          case 'suffix':
            break
        }
      },
      //道路输入联想
      getRoadLike(keywords){
        keywords = RegularStr(keywords);//输入的搜索内容(去除空格)
        if(keywords) {
          var $this = this;
          var addEditValue = $this.addEditValue;
          if(addEditValue.community&&addEditValue.street){
            var obj = {
              community: addEditValue.community,
              fields: 'road',
              keywords: keywords,
              street: addEditValue.street,
              // tablename:$this.tablename
            };
            $this.$api.getRoadLike(obj).then(function (res) {
              if (res.rows.length > 0) {
                var roadArr = [];
                for (var i = 0; i < res.rows.length; i++) {
                  roadArr.push(res.rows[i]['road'])
                }
                $this.radioArr.road = roadArr;
                $this.showOption = 'road'
              }

            }).catch(function (err) {

            })
          }

        }
      },
      //道路编号联想
      getRoadCodeLike(keywords){
        keywords = RegularStr(keywords);//输入的搜索内容(去除空格)
        if(keywords) {
          var $this = this;
          var addEditValue = $this.addEditValue;
          if(addEditValue.street&&addEditValue.community){
            var obj = {
              community: addEditValue.community,
              fields: 'road_num',
              keywords: keywords,
              street: addEditValue.street,
            };
            $this.$api.getRoadCodeLike(obj).then(function (res) {
              if (res.rows.length > 0) {
                var roadCodeArr = [];
                for (var i = 0; i < res.rows.length; i++) {
                  roadCodeArr.push(res.rows[i]['road_num'])
                }
                $this.radioArr.road_num = roadCodeArr;
                $this.showOption = 'road_num'
              }

            }).catch(function (err) {

            })
          }

        }
      },
      //小区输入联想
      getVillageLike(keywords){
        keywords = RegularStr(keywords);//输入的搜索内容(去除空格)
        if(keywords){
          var $this = this;
          var addEditValue = $this.addEditValue;
          if(!addEditValue.community||!addEditValue.street) return
            var obj = {
            community:addEditValue.community,
            fields:'village',
            keywords:keywords,
            street:addEditValue.street
          };
          $this.$api.getAddressLike(obj).then(function (res) {
            if(res.rows.length>0){
              var villageArr = [];
              for(var i=0;i<res.rows.length;i++){
                villageArr.push(res.rows[i]['village'])
              }
              $this.radioArr.village = villageArr;
              $this.showOption = 'village'
            }

          }).catch(function (err) {

          })
        }


      },
      //小区编号联想
      getCodeLike(keywords){
        keywords = RegularStr(keywords);//输入的搜索内容(去除空格)
        if(keywords) {
          var $this = this;
          var addEditValue = $this.addEditValue;
          if(!addEditValue.community||!addEditValue.street) return;
          var obj = {
            community: addEditValue.community,
            fields: 'code',
            keywords: keywords,
            street: addEditValue.street,
          };
          $this.$api.getCodeLike(obj).then(function (res) {
            if (res.rows.length > 0) {
              var codeArr = [];
              for (var i = 0; i < res.rows.length; i++) {
                codeArr.push(res.rows[i]['code'])
              }
              $this.radioArr.code = codeArr;
              $this.showOption = 'code'
            }

          }).catch(function (err) {

          })
        }
      },
      //确定添加一个地址
      handlerAddSite(){
        var $this = this;
        if($this.username){
          var opt = $this.addEditValue;
          if(opt.code&&opt.village&&opt.community&&opt.street&&opt.road&&opt.road_num){
            opt.username = $this.username;
            opt.originAddress = $this.editValue['originAddress'];
            $this.showAddLoad = true;
            $this.$api.addSite(opt).then(function (res) {
              $this.showAddLoad = false;
              $this.$Message.success('新增地址成功！');
              //将新增的地址添加到地址编辑的相似标准地址中
              var matchResult = $this.matchResult;
              var resArr = res.split(':');
              matchResult.unshift({
                address:resArr[0],
                similarity:resArr[1]
              });
              $this.matchResult = matchResult;
              $this.curSelAddress=res;
              $this.showAddModal = false
            }).catch(function (err) {
              $this.showAddLoad = false;
              $this.$Message.warning('新增地址失败，请稍后重试！')
            })
          }else{
            $this.$Message.warning('请填写完整！')
          }
        }else{
          $this.$Message.warning('请先登录！')
        }


      },

      //提交地址编辑(地址匹配)
      submitEdit() {
        var $this = this;
        if($this.username){
          var curSelAddress = $this.curSelAddObj.address;
          if(curSelAddress){
            $this.showELoad = true;
            var fid = $this.editValue['fid'];

            $this.$api.setEdit(fid,$this.username, curSelAddress).then(function (res) {

              if(res.update=='ok'){
                $this.showELoad = false;
                $this.$Message.success('保存成功！');
                $this.showEditModal = false;

                $this.listCount -=1 ;//总数
                $this.getAddByTarget($this.page)//刷新当前页

              }else{
                $this.showELoad = false;
                $this.$Message.warning('保存失败，请重试！')
              }
            }).catch(function (err) {
              $this.showELoad = false;
              $this.$Message.warning('保存失败，请重试！')
            })
          }else{
            $this.$Message.warning('请选择标准地址或新增一个地址！')
          }
        }else{
          $this.$Message.warning('请先登录！')
        }



      },

    },
    beforedestroy() {

    },
    watch: {
      username:{
        handler(val){
          if(val){
            //关闭编辑的窗口
            this.showAddModal=false;
            this.showEditModal=false;
          }
        },
        immediate: false
      },
      curSelAddress:{
        //被选中的相似标准地址，地址和相似度用,连接,将其转换为obj
        handler(val){
          var $this = this;
          if(val){
            var arr = val.split(':');
            $this.curSelAddObj = {
              address:arr[0],
              similarity:arr[1]
            }
          }else{
            $this.curSelAddObj = {}
          }
        }

      },
      needRefreshTarget: {
        //变化时刷新target列表
        handler(val) {
          var $this = this;
          if (val) {
            $this.curTarget = '';
            //查询数据源列表
            $this.$api.getTargetList($this.username).then(function (res) {
              if (res) {
                $this.targetList = res;
                $this.curEditIndex = -1;
                $this.showList = false;
              } else {
                $this.$Message.error('获取可下载的数据文件失败');
                $this.targetList = [];
              }
            }).catch(function (err) {
              $this.$Message.error('获取可下载的数据文件失败');
              $this.targetList = [];
            });
          } else {
          }
        },
        immediate: true
      }
    }
  }
</script>

<style scoped>
  .listView {
    width: 380px;
  }

  .listView .selector {
    margin-top: 20px;
    width: 100%;
    background: #3385ff;
    color: #fff;
    border-radius: 2px;
    box-shadow: 1px 2px 1px rgba(0, 0, 0, .15);
    height: 40px;
  }

  .selector .block {
    width: 50%;
    padding: 0 2%;
  }

  .selector .block:first-child {
    border-right: 1px solid #f2f2f2;
  }
  .selector .editBlock{
    border: none !important;
    width: 100%;
  }
  .selector .select {
    border-color: #fff;
    padding: 2px;
    width: 60%;
    border-radius: 3px;
  }

  .selector .toggle {
    flex-shrink: 0;
    padding: 0 10px;
    border-left: 1px solid #fff;
    cursor: pointer;
  }

  /*非标准地址列表*/
  .siteList {
    background: #fff;
    margin-top: 10px;
    border-radius: 4px;
    width: 100%;
    padding: 0 0 10px;
  }

  .siteList li {
    height: 45px;
    justify-content: flex-start;
    cursor: pointer;
    padding: 0 10px;
  }

  .siteList .sel,.siteList li:hover {
    color: #fff;
    background: rgba(0, 0, 0, 0.2);
  }

  .siteList li span {
    width: 100%;
    padding: 2px 0;
  }

  .siteList li .imgItem {
    flex-shrink: 0;
    width: 20px;
    margin-left: 10px;
  }

  /*分页*/
  .pageClass {
    padding: 5px 10px 0;
    border-top: 1px solid #f2f2f2;
  }


  /*编辑对话框*/
  .editModal .ivu-modal {
    position: absolute;
    top: 120px;
    right: 0;
  }

  .editLine {
    justify-content: flex-start;
    width: 100%;
    height: 45px;
    border-bottom: 2px solid #f2f2f2;
    position: relative;
  }

  .editLine .label {
    background: #3385ff;
    color: #fff;
    height: 100%;
    width: 100px;
    justify-content: center;
    flex-shrink: 0;
  }
  .editLine .labelWrap{
    width: 100px;
    justify-content: center;
    flex-shrink: 0;
  }
  .editLine .labelWrap .label{
    width: 100%;
  }
  .editLine .canEdit {
    border: 1px solid #7f7f7f;
  }
  .editLine .canAddEdit{
    border: 1px solid #E8EBED ;
  }
  .editLine .text {
    background: none;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .autoHeightWrap .label {
    /*height: auto;*/
    /*min-height: 35px;*/
  }
  .autoHeightWrap{
    height: auto;
    background:#3385ff ;
  }
  .editLine .autoHeight {
    height: auto;
    min-height: 35px;
    padding-left: 10px;
    width: 100%;
    background: #fff;
    justify-content: flex-start;
  }

  .editLine .text, .editLine .textarea{
    margin-left: 10px;
    height: 80%;
    width: 100%;
    padding-left: 5px;
  }

  .selectContainer {
    height: 45px;
  }

  .editLine .selectWrapper {
    position: relative;
    padding: 5px 0 10px 5px;
  }
  /*相似度*/
  .similar{
    border-left: 1px solid #f3f3f3;
    color: #3385ff;
    text-align: center;
  }
  .similar_row{
    border-top: 1px solid #f3f3f3;
    color: #3385ff;
    text-align: center;
  }
  .editLine .select {
    width: 100%;
    height: auto;
  }

  .editLine:last-child{
    border: none;
  }
  .addLine{
    cursor: pointer;
  }
  .editLine .input{
    margin-left: 0.5rem;
  }
  /*编辑*/
  .editLine .editBtnWrap{
    margin-left: 5px;
  }
  .editLine .options{
    position: absolute;
    top: 95%;
    right: 0;
    background: #fff;
    padding: 0 5px;
    border: 1px solid #969696;
    z-index: 10;
    border-radius: 5px;
    width: 69%;
    max-height: 300px;
    overflow-y: auto;
    overflow-x: hidden;
  }
  .editLine .otherOpt{
    width: 61.5%;
    right: 25px;
  }
</style>



